<template>
  <div>
    <div
      class="tabTop bg-gradient-to-l from-cyan-600 to-violet-800  flex justify-between items-center p-2 opacity-90 rounded-b-lg"
    >
      <van-icon name="more-o" class="text-white text-2xl hover:animate-spin" @click="moreShowOn" />
      <div
        class="h-8 w-80 bg-white rounded-2xl flex justify-center items-center text-coolGray-900 font-mono rounded mx-4 animate-pulse"
        @click="$router.push('/search')"
      >
        <div>
          <van-icon name="search" class="text-lg align-middle mr-1" />
          <span class="align-middle">搜索</span>
        </div>
      </div>
      <van-icon name="vip-card-o" class="text-white text-2xl" @click="pay"/>
    </div>
    <van-tabs v-model="active"  @change="changeChannel" title-active-color="#0891b2" color="#0891b2" background="" line-height="2px" class="text-cyan-500">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="w-full">
        <van-tab
          v-for="(cost, index) in list"
          :title="cost.name"
          :name="index"
          :key="cost.id"
        >
          <van-list
            v-model="listloading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
            offset="10"
            class="px-3 box-border pb-16"
            v-if="articleList[active] && articleList[active].length != 0"
          >
            <van-swipe-cell v-for="(item, index) in articleList[active]" :key="item.id">
              <div class="w-full py-4 px-1 my-2 border-1 shadow-md rounded-md flex flex-col justify-between items-start" >
                <div class="flex justify-between w-full py-2" @click="details(item)">
                  <p class="van-multi-ellipsis--l3 text-sm font-mono font-bold text-gray-600" >{{ item.title }}</p>
                  <img :src="item.cover.images[0]" v-if="item.cover.images.length != 0" alt="" class="w-28 h-16 rounded mr-1 shadow-md">
                </div>
                <div class="text-xs text-gray-400 flex justify-between w-full items-center">
                  <span>来自：{{ item.aut_name }}</span>
                  <span>{{ item.comm_count }}评论</span>
                  <span>{{ item.pubdate | dayfilter }}</span>
                  <van-icon name="close" class="pr-1 text-sm" @click="close(item, index)"/>
                </div>
              </div>
              <template #right>
                <div
                  class="flex flex-col justify-around items-center w-full h-full pl-2 relative"
                >
                  <button
                    class="bg-pink-500 w-16 h-10 focus:outline-none rounded-md text-white"
                  >
                    收藏
                  </button>
                  <button
                    class="bg-indigo-900 w-16 h-10 focus:outline-none rounded-md text-white"
                    is-link
                    @click="show = true"
                  >
                    举报
                  </button>
                </div>
              </template>
            </van-swipe-cell>
            <template #loading>
              <van-loading size="24px">别催辣，人家正在努力加载~~</van-loading>
            </template>
          </van-list>
          <div v-else-if="loadYes" class="relative">
            <i class="icon iconfont absolute right-0 -top-40 text-5xl transform text-yellow-500 animate-bounce ">&#xe644;</i>
            <h1  class="text-center mt-48 font-mono font-bold text-rose-500 text-2xl">数据走丢了~充个钱看看？</h1>
          </div>
          <van-loading v-else size="24px" class="text-center mt-48 font-mono font-bold ">加载中...</van-loading>
        </van-tab>
      </van-pull-refresh>
    </van-tabs>
  <!--  充值  -->
  <van-popup v-model="show" class="rounded-md">
      <div :class="['w-72 rounded-b-full bg-white p-6 text-center flex flex-col', {'animate__animated animate__rubberBand': show}]">
        <img src="../../../assets/drink.jpg" alt="">
        <span class="font-bold font-mono text-cyan-500">请作者喝个咖啡吧</span>
        <span class="font-bold font-mono text-cyan-500">你不给钱我很难办事啊</span>
        <div class="text-center">
          <button class="mt-6 w-20 h-20 rounded-full bg-purple-700 text-pink-500 font-mono font-bold focus:ring-2 focus:outline-none focus:ring-offset-2 focus:ring-blue-600 transform focus:animate-bounce">
            点击投币
          </button>
        </div>
      </div>
    </van-popup>
  <van-popup v-model="payPupopShow" class="rounded-md">
    <div class="w-72 rounded-b-full bg-white p-6 text-center flex flex-col">
      <img src="../../../assets/drink.jpg" alt="">
      <span class="font-bold font-mono text-cyan-500">请作者喝个咖啡吧</span>
      <span class="font-bold font-mono text-cyan-500">你不给钱我很难办事啊</span>
      <div class="text-center">
        <button class="mt-6 w-20 h-20 rounded-full bg-purple-700 text-pink-500 font-mono font-bold focus:ring-2 focus:outline-none focus:ring-offset-2 focus:ring-blue-600 transform focus:animate-bounce">
          点击投币
        </button>
      </div>
    </div>
  </van-popup>
  <button class="fixed left-2 top-1/2 bg-gray-300 w-8 h-8 rounded-full button-o focus:ring-coolGray-600 opacity-20 focus:opacity-50 transform focus:scale-110 flex justify-center items-center" @click="left">
    <van-icon name="arrow-left" />
  </button>
  <button class="fixed right-2 top-1/2 bg-gray-300 w-8 h-8 rounded-full button-o focus:ring-coolGray-600 opacity-20 focus:opacity-50 transform focus:scale-110 flex justify-center items-center" @click="right">
    <van-icon name="arrow" />
  </button>
  <van-popup v-model="moreShow" position="bottom" class="h-3/4 rounded-t-2xl p-4 font-mono">
    <div class="flex flex-col flex-wrap ">
      <h1 class="flex-none font-bold flex justify-between">
        用户列表
        <button :class="['icon iconfont font-normal text-rose-400 text-sm button-o rounded-md border-2 transform px-1 border-rose-400 focus:ring-offset-0 focus:ring-0', {'scale-110': ableEditList}]" @click="editList">{{ ableEditList ? '完成' : '编辑' }}</button></h1>
      <ul class="flex flex-wrap my-2">
        <li v-for="(item, index) in list" :key="item.id" class="p-1 my-1 mx-2 rounded-md bg-gray-100 text-lightBlue-500 text-sm border-coolGray-200 border-2 relative" @click="userCannelList(index)">
          {{ item.name }}
          <van-icon name="cross" class="absolute -top-1.5 -right-1.5" v-if="ableEditList" @click.stop="deleteUserCannelList (item, index)"/>
        </li>
      </ul>
    </div>
    <div class="flex flex-col flex-wrap ">
      <h1 class="flex-none font-bold">
        <span>全部列表</span>
      </h1>
      <ul class="flex flex-wrap my-2">
        <li v-for="(item, index) in allList" :key="item.id" class="p-1 my-1 mx-2 rounded-md bg-blueGray-50 text-amber-500 text-sm border-coolGray-200 border-2" @click="allCannelList(item, index)">
          {{ item.name }}
        </li>
      </ul>
    </div>
  </van-popup>
  </div>
</template>

<script>

import { getChannel, getArticleList, getAllChannel, deleteCannel, addCannel } from '@/api/indexApi'
import { getToken } from '@/utils/token'
export default {
  name: 'index',
  data () {
    return {
      // 下拉刷新
      isLoading: false,
      // 上滑刷新
      listloading: false,
      finished: false,
      // tab绑定
      active: 0,
      // 频道列表
      list: [],
      // 文章列表
      articleList: [],
      // 喝咖啡
      show: false,
      // 频道管理
      moreShow: false,
      // 充值
      payPupopShow: false,
      // 所有频道列表
      allList: [],
      // 能不能编辑啊
      ableEditList: false,
      // 要不要显示没数据啊
      loadYes: false
    }
  },
  methods: {
    // 下拉刷新
    async onRefresh () {
      // 如果active为0则传递0作为name参数
      const res = await this.changeChannel(this.active ? '666' : 0, '刷新', 0)
      console.log(res)
      this.$toast.success('刷新成功')
      this.isLoading = false
    },
    // 列表上拉刷新
    async onLoad () {
      this.listloading = true
      // 延迟加载，节流
      setTimeout(async () => {
        const res = await this.changeChannel(this.active ? '666' : 0, '加载更多', 0)
        console.log(res)
        this.$toast.success('刷新成功')
        this.listloading = false
      }, 800)
    },
    // 改变列表显示后，重新请求
    async changeChannel (name, title, top = 1) {
      // 当前选中的item的id，如果是第一次，list为空，就用name作为参数
      const res = await getArticleList({
        channel_id: this.list[this.active]?.id || name,
        timestamp: new Date().getTime(),
        with_top: top
      })
      // 如果数据长度为0，显示无数据(并已经加载完成)
      if (res.data.results.length === 0) this.loadYes = true
      // 难点，用【】会导致不触发响应式
      if (title === '加载更多') {
        this.articleList[this.active].push(...res.data.results)
      } else {
        this.articleList.splice(this.active, 1, res.data.results)
      }
      return res
    },
    // 点击跳转到详情
    details (value) {
      console.log(value)
      const id = value.art_id
      console.log(id)
      this.$router.push(`/details/${id}`)
    },
    // 左滑
    left () {
      if (this.active >= 1) {
        this.active -= 1
      }
      this.changeChannel()
    },
    // 右滑
    right () {
      if (this.active < this.list.length) {
        this.active += 1
      }
      this.changeChannel()
    },
    // 关闭文章的显示
    close (item, index) {
      this.$toast('此文章已经关入小黑屋~')
      this.articleList[this.active].splice(index, 1)
    },
    // 全部频道列表页
    async moreShowOn () {
      this.moreShow = true
      const res = await getAllChannel()
      console.log(res)
      this.allList = res.data.channels
      // 筛选，如果是用户已经有的频道则不显示，但是时间复杂度太高，O（n2）
      this.list.forEach(value => {
        this.allList.forEach((item, index) => {
          if (item.id === value.id) {
            this.allList.splice(index, 1)
          }
        })
      })
    },
    // 控制是否开启编辑的开关
    editList () {
      this.ableEditList = !this.ableEditList
    },
    // 切换列表
    userCannelList (index) {
      if (this.ableEditList) {
        this.active = index
        this.changeChannel()
      }
    },
    // 全部列表的频道添加到用户列表
    allCannelList (item, index) {
      if (getToken()) {
        if (this.ableEditList) {
          try {
            this.list.push(this.allList.splice(index, 1)[0])
          } catch (e) {
            console.log(e)
          }
          addCannel({
            id: item.id,
            seq: this.list.length - 1
          }).then(res => {
            console.log(res)
          }).catch(error => {
            console.log(error)
          })
        }
      } else {
        this.$toast.fail('你丫还没登录呢，想咋的？')
      }
    },
    // 删除用户列表，添加到全部列表
    deleteUserCannelList (item, index) {
      if (getToken()) {
        if (this.ableEditList) {
          try {
            this.allList.push(this.list.splice(index, 1)[0])
          } catch (e) {
            console.log(e)
          }
          deleteCannel(item.id).then(res => {
            console.log(res)
          }).catch(error => {
            console.log(error)
          })
        }
      } else {
        this.$toast.fail('删不了，你丫还没登录呢')
      }
    },
    pay () {
      this.payPupopShow = true
    }
  },
  // 初始化，加载用户频道列表，获取初始文章
  mounted () {
    getChannel().then(res => {
      this.list = res.data.channels
      this.articleList.length = this.list.length
    })
    this.changeChannel(0)
  }
}
</script>
